import React  from 'react';
import { FaBars } from 'react-icons/fa';

import { Nav, NavbarContainer, NavLogo, MobileIcon, NavItem, NavLinks, NavMenu, NavBtn, NavBtnLink } from './NavbarElement';

const Navbar =({showMenu,changeShowMenu})=>{
  
    const handleClick=()=>{
        
        changeShowMenu()
        console.log('Icon clicked')

    }
            return (
            <Nav >
                <NavbarContainer >
                    <NavLogo to="/"><img id="logos" src="https://i.ibb.co/XWfMPj1/1479458-1419074164994972-1219290860-n-removebg-preview.png" alt="safety-logo" /></NavLogo>
                    <MobileIcon onClick={handleClick} >
                        <FaBars />
                    </MobileIcon >
                    {showMenu ? <NavMenu >
                        <NavItem>
                            <NavLinks to="/"  > Home </NavLinks>
                        </NavItem>
                       
                        <NavItem>
                            <NavLinks to="/about"  > About </NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to="/discover" > Discover </NavLinks>
                        </NavItem>
                        <NavItem>
                            <NavLinks to="/services" > services </NavLinks>
                        </NavItem>
                        
                        <NavBtn>
                            <NavBtnLink to="/Sign-In" > Login </NavBtnLink>
                        </NavBtn>
                        
                        <NavBtn>
                            <NavBtnLink to="/Sign-Up" > Register</NavBtnLink>
                        </NavBtn>
                    </NavMenu> : null
                    }

                </NavbarContainer>
            </Nav>
        )
}

export default Navbar

